<script>
//import Footer from "@/components/Footer";
import Header from "@/components/Header";
import LineCard from "@/components/line";

import BScroll from "better-scroll"; //better-scroll

export default {
  name: "index",
  components: { Header, LineCard },
  data() {
    return {
      current: 0,
      hotelNmae: "",
      labels: ["", "", "", "5-29"],
      price: "3888",
      starList: [{}, {}, {}, {}, {}]
    };
  },
  mounted() {
    this.$nextTick(() => {
      let timer = setTimeout(() => {
        // $nextTick
        if (timer) {
          clearTimeout(timer);
          this.verScroll();
        }
      }, 0);
    });
  },

  methods: {
    verScroll() {
      let width = this.starList.length * 86; // 
      this.$refs.cont.style.width = width + "px"; // 
      this.$nextTick(() => {
        if (!this.scroll) {
          this.scroll = new BScroll(this.$refs.wrapper, {
            startX: 0, // better-scroll
            click: true,
            scrollX: true,
            scrollY: false,
            eventPassthrough: "vertical"
          });
        } else {
          this.scroll.refresh(); //dom
        }
      });
    },
    onChange(index) {
      this.current = index;
    },
    onChanges(index) {
      this.current = index;
    }
  }
};
</script>

